.page {
  scroll-behavior: smooth;
  background: $bg-color;
  font-family: "Inter", sans-serif;

  &__body {
    margin: 0;
    min-width: 320px;
    max-width: $desktop-min-width;
    margin-inline: auto;
  }

  &__menu {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;

    transition: all 0.3s;
    opacity: 0;
    transform: translateX(100%);
    pointer-events: none;

    &:target {
      opacity: 1;
      transform: translateX(0);
      pointer-events: all;
    }
  }

  &:has(.page__menu:target) {
    overflow: hidden;
  }
}
